<template>
  <div id="PieChart">
    <charts
      type="bar"
      width="500"
      height="350"
      :options="chartOptions"
      :series="series"
      :key="change"
    />
  </div>
</template>

<script>
// @ is an alias to /src
import VueApexCharts from "vue-apexcharts";
import axios from "axios";

export default {
  name: "PieChart",
  components: {
    charts: VueApexCharts
  },
  props: {
    clientName: {},
    timeperiod: {}
  },
  watch: {
    clientName: function(newVal) {
      this.getData(this, newVal, this.timeperiod);
    },
    timeperiod: function(newVal) {
      this.getData(this, this.clientName, newVal);
    }
  },
  data() {
    return {
      change: false,
      series: [
        {
          data: []
        }
      ],
      chartOptions: {
        chart: {
          toolbar: {
            show: false
          }
        },
        plotOptions: {
          bar: {
            horizontal: true
          }
        },
        dataLabels: {
          enabled: false
        },
        xaxis: {
          categories: []
        }
      }
    };
  },
  methods: {
    getData: (self, client = "", days = "") => {
      axios
        .get(
          process.env.VUE_APP_DEV +
            `dashboard.xsjs?api=countkpi&user=${client}&selection=${days}`,
          {
            withCredentials: true
          }
        )
        .then(response => {
          self.chartOptions.xaxis.categories = [];
          self.series[0].data = [];

          response.data.forEach(item => {
            self.chartOptions.xaxis.categories.push(item.apiName);
            self.series[0].data.push(parseInt(item.count));
          });
          self.change = !self.change;
        })
        .catch(e => alert(JSON.stringify(e.response.data)));
    }
  },
  mounted() {
    this.getData(this);
  }
};
</script>

<style scoped>
#PieChart {
  max-height: 280px;
  overflow: auto;
}

#PieChart::-webkit-scrollbar {
  width: 0.5em;
}

#PieChart::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

#PieChart::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}
</style>
