import Vue from "vue";
import Router from "vue-router";
import Dashboard from "./components/Dashboard.vue";
import HazardLayer from "./components/HazardLayer.vue";
import JobLogs from "./components/JobLogs.vue";
import ClientMgmt from "./components/ClientMgmt.vue";

Vue.use(Router);

const routes = [
  {
    path: "/dashboard",
    name: "Dashboard",
    component: Dashboard
  },
  {
    path: "/hazardlayer",
    name: "HazardLayer",
    component: HazardLayer
  },
  {
    path: "/joblogs",
    name: "JobLogs",
    component: JobLogs
  },
  {
    path: "/clientmgmt",
    name: "ClientManagement",
    component: ClientMgmt
  }
];

export default new Router({
  mode: "abstract",
  base: process.env.BASE_URL,
  routes: routes
});
