<template>
  <div id="LineBarAreaChart">
    <div>Performance Trend for {{selection.replace('-','')}}</div>
    <charts type="bar" :options="chartOptions" :series="series" height="330px" :key="change"></charts>
  </div>
</template>

<script>
// @ is an alias to /src
import VueApexCharts from "vue-apexcharts";
import axios from "axios";

export default {
  name: "LineBarAreaChart",
  components: {
    charts: VueApexCharts
  },
  props: {
    selection: {},
    clientName: {},
    timeperiod: {},
    apiName: {}
  },
  watch: {
    clientName: function(newVal) {
      if (newVal !== "" && this.apiName !== "")
        this.getChartData(this, newVal, this.timeperiod, this.apiName);
      else this.getData(this, newVal, this.timeperiod);
    },
    timeperiod: function(newVal) {
      if (newVal !== "" && this.apiName !== "")
        this.getChartData(this, this.clientName, newVal, this.apiName);
      else this.getData(this, this.clientName, newVal);
    },
    apiName: function(newVal) {
      if (newVal !== "")
        this.getChartData(this, this.clientName, this.timeperiod, newVal);
      else this.getData(this);
    }
  },
  data() {
    return {
      change: true,
      chartOptions: {
        chart: {
          toolbar: {
            show: false
          }
        },
        dataLabels: {
          enabled: false
        },
        stroke: {
          curve: "straight"
        },

        grid: {
          row: {
            colors: ["#f3f3f3", "transparent"], // takes an array which will be repeated on columns
            opacity: 0.5
          }
        },
        xaxis: {
          categories: []
        }
      },
      series: [
        {
          name: "Time",
          data: []
        }
      ]
    };
  },
  methods: {
    getData: (self, client = "", days = "") => {
      axios
        .get(
          process.env.VUE_APP_DEV +
            `dashboard.xsjs?api=perfkpi&user=${client}&selection=${days}`,
          {
            withCredentials: true
          }
        )
        .then(response => {
          self.chartOptions.xaxis.categories = [];
          self.series[0].data = [];

          response.data.forEach(item => {
            self.chartOptions.xaxis.categories.push(item.name);
            self.series[0].data.push(
              parseFloat(item.avgTime) === null ? 0 : parseFloat(item.avgTime)
            );
          });
          self.change = !self.change;
        })
        .catch(e => alert(JSON.stringify(e.response.data)));
    },
    getChartData: (self, client = "", days = "", apiName = "") => {
      axios
        .get(
          process.env.VUE_APP_DEV +
            `dashboard.xsjs?api=perfchart&user=${client}&selection=${days}&name="${apiName}"`,
          {
            withCredentials: true
          }
        )
        .then(response => {
          self.chartOptions.xaxis.categories = [];
          self.series[0].data = [];

          response.data.forEach(item => {
            self.chartOptions.xaxis.categories.push(item.label);
            self.series[0].data.push(
              parseFloat(item.avgTime) === null ? 0 : parseFloat(item.avgTime)
            );
          });
          self.change = !self.change;
        })
        .catch(e => alert(JSON.stringify(e.response.data)));
    }
  },
  mounted() {
    this.getData(this);
  }
};
</script>

<style scoped>
#LineBarAreaChart div {
  padding-top: 10px;
  font-family: "IBM Plex Sans", Verdana, Tahoma;
  letter-spacing: 0.02em;
  font-size: 1.1em;
  font-weight: bold;
  color: #3e606f;
}
</style>
