<template>
  <div id="ClientMgmt">
    <div class="popup" v-show="resetClient !== undefined">
      <div class="popupheader">
        <img src="@/assets/error.png">
        <label>Password Reset</label>
      </div>
      <div class="popupcontent">
        <label>
          Reset of password for
          <i>{{resetClient}}</i> will have impact on RiskSuite Application, Do you want to continue? If yes, enter approprite comments below and click
          <b>Reset</b>
        </label>
        <textarea
          rows="4"
          cols="49"
          placeholder="Enter your comments here..."
          v-model="resetComments"
        ></textarea>
      </div>
      <div class="popupfooter">
        <button @click="restPassword()">Reset</button>
        <button @click="resetClient = undefined">Cancel</button>
      </div>
    </div>

    <div class="popupaddclient" v-show="addNewClient !== undefined">
      <div class="popupheader">
        <img src="@/assets/newclient.png">
        <label>Create Technical User for New Client</label>
      </div>
      <div class="popupcontent">
        <div class="inputfields">
          <label class="label">Client Name</label>
          <input type="text" class="input" v-model="clientName" required pattern=".*\S.*">
          <span class="inputborder"></span>
        </div>
        <div class="inputfields">
          <label class="label">Application Username</label>
          <input type="text" class="input" v-model="appUserName" required pattern="[^\s]*">
          <span class="inputborder"></span>
        </div>
        <div class="inputfields">
          <label class="label">Tableau Username</label>
          <input type="text" class="input" v-model="tabUserName" required pattern="[^\s]*">
          <span class="inputborder"></span>
        </div>
      </div>
      <div class="popupfooter">
        <button @click="createUser(1)">Activate</button>
        <button @click="createUser(0)" disabled>Draft</button>
        <button @click="addNewClient = undefined">Cancel</button>
      </div>
    </div>

    <div class="popup" v-show="password !== undefined">
      <div class="popupheader">
        <img src="@/assets/key.png">
        <label>New Password..!</label>
      </div>
      <div class="passwordpopupcontent">
        <span>
          <label>{{password}}</label>
        </span>
        <transition name="fade">
          <label v-show="copied">Copied...!</label>
        </transition>
      </div>
      <div class="popupfooter">
        <button @click="copyToClipboard(password)">Copy to Clipboard</button>
        <button @click="password = undefined">Close</button>
      </div>
    </div>

    <span class="heading">
      <div>Client Management</div>
      <img src="@/assets/add.png" @click="addNewClient = 1">
    </span>
    <div class="clientdetails clientheader">
      <span>Client</span>
      <span>Application User (Tech.)</span>
      <span>Tableau User (Tech.)</span>
      <span>Last Modification</span>
      <span>Status</span>
    </div>
    <span v-for="(item, index) in clientList" :key="index">
      <div class="clientdetails">
        <span>
          <input :value="item.clientName" readonly>
        </span>
        <span>
          <input :value="item.appUserName" readonly>
          <img src="@/assets/reset.png" @click="resetClient = item.appUserName">
        </span>
        <span>
          <input :value="item.tableauUserName" readonly>
          
          <img src="@/assets/reset.png" @click="resetClient = item.tableauUserName">
        </span>
        <span>
          <input :value="item.lastModification" readonly>
        </span>
        <span>
          <input
            :value="item.status ===1 ? 'Active' : 'Inactive'"
            readonly
            style="text-align: center"
          >
        </span>
      </div>
    </span>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: {},
  components: {},
  data() {
    return {
      password: undefined,
      copied: false,
      resetComments: "",
      resetClient: undefined,
      addNewClient: undefined,
      clientList: [],
      clientName: "",
      appUserName: "",
      tabUserName: ""
    };
  },
  methods: {
    async restPassword() {
      if (
        this.resetComments === undefined ||
        this.resetComments.trim() === ""
      ) {
        alert("Enter your comments for reset of password");
      } else {
        let payload = {
          userName: this.resetClient,
          comment: this.resetComments
        };
        await axios
          .post(
            process.env.VUE_APP_DEV + `usermgmt.xsjs?type=reset`,
            JSON.stringify(payload),
            {
              withCredentials: true
            }
          )
          .then(response => {
            if (response.data.status === "Reset Successfull") {
              this.resetClient = undefined;
              this.resetComments = "";
              this.password = response.data.password;
            } else {
              alert(response.data.error);
            }
          })
          .catch(e => alert(JSON.stringify(e.response.data)));
      }
    },
    async createUser(isactive) {
      if (this.clientName.trim() === "") {
        alert("Please enter Client Name");
      } else if (this.appUserName.trim().indexOf(" ") > 0) {
        alert("Application Username cannot have space");
      } else if (this.tabUserName.trim().indexOf(" ") > 0) {
        alert("Tableau Username cannot have space");
      } else if (this.tabUserName.trim() === "") {
        alert("Tableau Username cannot be empty");
      } else if (this.appUserName.trim() === "") {
        alert("Application Username cannot be empty");
      } else if (this.appUserName.trim() === this.tabUserName.trim()) {
        alert("Application Username cannot same as Tableau Username");
      } else {
        if (isactive === 1) {
          let payload = {
            clientName: this.clientName,
            appUser: this.appUserName,
            tableauUser: this.tabUserName
          };
          await axios
            .post(
              process.env.VUE_APP_DEV + `usermgmt.xsjs?type=create`,
              JSON.stringify(payload),
              {
                withCredentials: true
              }
            )
            .then(response => {
              if (response.data.status === "Created") {
                this.addNewClient = undefined;
                this.appUserName = "";
                this.tabUserName = "";
                this.clientName = "";
                this.password = response.data.password;
                this.loadUserDetails();
              } else {
                alert(response.data.error);
              }
            })
            .catch(e => {
              if (e.response.data.status) {
                alert(JSON.stringify(e.response.data.error));
              } else {
                alert(JSON.stringify(e.response.data));
              }
            });
        }
      }
    },

    async copyToClipboard(str) {
      const el = document.createElement("textarea");
      el.value = str;
      document.body.appendChild(el);
      el.select();
      document.execCommand("copy");
      document.body.removeChild(el);
      this.copied = true;
      await (() => {
        this.copied = true;
      });
      setTimeout(() => {
        this.copied = false;
      }, 1500);
    },

    async loadUserDetails() {
      await axios
        .get(process.env.VUE_APP_DEV + `usermgmt.xsjs`, {
          withCredentials: true
        })
        .then(response => {
          this.clientList = response.data;
        })
        .catch(e => alert(JSON.stringify(e.response.data)));
    }
  },
  created() {},
  mounted() {
    this.loadUserDetails();
  }
};
</script>

<style scoped>
