<template>
  <div id="Dashboard">
    <span class="heading">
      <div>Dashboard</div>
    </span>
    <span class="client">
      <span v-for="(item, index) in client" :key="index">
        <div
          :class="{'selected': item.TECHNICAL_USER === selectedClient}"
          @click="selectedClient=item.TECHNICAL_USER"
        >{{item.description}}</div>
      </span>
    </span>
    <div class="metrics">
      <div class="performanance">
        <div class="perfheader">
          <div class="title">Api Performanance Metric</div>
          <div class="selection">
            <vue-slider v-bind="sliderOption" v-model="sliderSelection"></vue-slider>
          </div>
        </div>
        <span class="apidetails">
          <table>
            <th>Api Name</th>
            <th>Min(ms)</th>
            <th>Avg(ms)</th>
            <th>Max(ms)</th>
            <tr
              v-for="(api, index) in apiData"
              :key="index"
              :class="{'selectedapi': selectedServiceindex === index}"
              @click="selectedServiceindex = index"
            >
              <td>{{api.name}}</td>
              <td>{{api.minTime}}</td>
              <td>{{api.avgTime}}</td>
              <td>{{api.maxTime}}</td>
            </tr>
          </table>
        </span>
      </div>
      <div class="chart">
        <div class="title">Api Call(count) Shares by Client and type</div>
        <BarChart :clientName="selectedClient" :timeperiod="getTimeperiod(sliderSelection)"/>
      </div>
    </div>
    <div class="chart">
      <LineChart
        :selection="sliderSelection"
        :clientName="selectedClient"
        :timeperiod="getTimeperiod(sliderSelection)"
        :apiName="getSelectedApi(selectedServiceindex)"
      />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import vueSlider from "vue-slider-component";
import LineChart from "@/components/LineBarAreaChart.vue";
import BarChart from "@/components/BarChart.vue";

export default {
  props: {},
  components: {
    vueSlider,
    LineChart,
    BarChart
  },
  data() {
    return {
      selectedServiceindex: -1,
      apiData: [
        {
          name: "collection.xsjs",
          minTime: 300,
          avgTime: 340,
          maxTime: 600
        }
      ],
      sliderOption: {
        value: 1,
        tooltip: "hover",
        disabled: false,
        piecewise: true,
        piecewiseLabel: true,
        height: "2",
        style: {
          marginLeft: "10%",
          fontFamily: "IBM Plex Sans"
        },
        data: ["-30Days", "-15Days", "-7Days", "-48Hrs", "Today"],
        piecewiseStyle: {
          backgroundColor: "#ccc",
          visibility: "visible",
          width: "12px",
          height: "12px"
        },
        piecewiseActiveStyle: {
          backgroundColor: "#3498db"
        },
        labelActiveStyle: {
          color: "#3498db"
        }
      },
      sliderSelection: "30Days",
      selectedClient: "",
      client: [
        {
          description: "MIND_MASTER_DEV",
          TECHNICAL_USER: "MIND_MASTER_DEV"
        }
      ]
    };
  },
  methods: {
    getSelectedApi(index) {
      return index === -1 ? "" : this.apiData[index].name;
    },
    getTimeperiod(data) {
      var timeperiod = 0;
      switch (data) {
        case "-30Days":
          timeperiod = 30;
          break;
        case "-15Days":
          timeperiod = 15;
          break;
        case "-7Days":
          timeperiod = 7;
          break;
        case "-48Hrs":
          timeperiod = 2;
          break;
        default:
          timeperiod = 0;
      }
      return timeperiod;
    }
  },
  created() {},
  async mounted() {
    await axios
      .get(process.env.VUE_APP_DEV + `dashboard.xsjs?api=client`, {
        withCredentials: true
      })
      .then(response => {
        this.client = response.data;
      })
      .catch(e => alert(JSON.stringify(e.response.data)));

    await axios
      .get(
        process.env.VUE_APP_DEV + `dashboard.xsjs?api=perfkpi&user=&selection=`,
        {
          withCredentials: true
        }
      )
      .then(response => {
        this.apiData = response.data;
      })
      .catch(e => alert(JSON.stringify(e.response.data)));
  }
};
</script>

